/** Reset CSS */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --background-color: #67e8f9;
  --primary-text-color: #020617;
  --card-bg-color: #ffffff;
}

body {
  background-color: var(--background-color);
  overflow-x: hidden;
}

.header {
  display: flex;
  justify-content: center;
  padding: 1rem;
  position: fixed;
  width: 100vw;
  z-index: 1;
  background-color: var(--background-color);
}

.main {
  display: flex;
  gap: 1.5rem;
  flex-wrap: wrap;
  padding: 8rem 2rem 2rem 7rem;
}